import path from 'path'
import os from 'os'
import { app, ipcMain, Tray, Menu, nativeImage, globalShortcut, Notification, screen } from 'electron'
import serve from 'electron-serve'
import { createWindow } from './helpers'

const isProd = process.env.NODE_ENV === 'production'

if (isProd) {
  serve({ directory: 'app' })
} else {
  app.setPath('userData', `${app.getPath('userData')} (development)`)
}

let tray: Tray | null = null
let mainWindow: any = null
let overlayWindow: any = null

  ; (async () => {
    await app.whenReady()

    // 1.   ( )
    mainWindow = createWindow('main', {
      width: 400,
      height: 300,
      show: false, //  
      frame: false,
      alwaysOnTop: true,
      webPreferences: {
        preload: path.join(__dirname, 'preload.js'),
      },
    })

    // 2.   ( )
    const primaryDisplay = screen.getPrimaryDisplay()
    const { width, height } = primaryDisplay.workAreaSize

    overlayWindow = createWindow('overlay', {
      width,
      height,
      x: 0,
      y: 0,
      transparent: true,
      frame: false,
      alwaysOnTop: true,
      show: false,
      webPreferences: {
        preload: path.join(__dirname, 'preload.js'),
      },
    })
    overlayWindow.setIgnoreMouseEvents(true)

    const port = process.argv[2]
    if (isProd) {
      await mainWindow.loadURL('app://./home')
      await overlayWindow.loadURL('app://./overlay')
    } else {
      await mainWindow.loadURL(`http://localhost:${port}/home`)
      await overlayWindow.loadURL(`http://localhost:${port}/overlay`)
      // mainWindow.webContents.openDevTools()
    }

    // 3.   
    const iconPath = path.join(__dirname, '../renderer/public/images/logo-icon.png')
    const icon = nativeImage.createFromPath(iconPath).resize({ width: 16, height: 16 })
    tray = new Tray(icon)
    const contextMenu = Menu.buildFromTemplate([
      { label: '  (Alt+S)', click: () => showSender() },
      { type: 'separator' },
      {
        label: '  ', type: 'checkbox', checked: app.getLoginItemSettings().openAtLogin, click: (item) => {
          app.setLoginItemSettings({ openAtLogin: item.checked })
        }
      },
      { label: '', click: () => app.quit() }
    ])
    tray.setToolTip(' (COOCON-PANG)')
    tray.setContextMenu(contextMenu)

    // 4.   
    globalShortcut.register('Alt+S', () => {
      showSender()
    })

    //    
    app.setLoginItemSettings({ openAtLogin: true })

  })()

function showSender() {
  if (mainWindow) {
    mainWindow.show()
    mainWindow.center()
    mainWindow.focus()
  }
}

// !    (IPC   )
ipcMain.on('trigger-pang', (event, data) => {
  // Phase 1: Tray Update
  if (tray) {
    const pangIcon = path.join(__dirname, '../renderer/public/images/logo-icon.png') // TODO:     
    // tray.setImage(...) 
  }

  // Phase 2: Show Overlay & Fireworks
  if (overlayWindow) {
    overlayWindow.show()
    overlayWindow.webContents.send('start-fireworks', data)
    setTimeout(() => {
      overlayWindow.hide()
    }, 4500) //    
  }

  // Phase 3: Native Notification
  const title = data.isAnonymous
    ? ' []  '
    : ` [] ${data.sender} !`

  const notification = new Notification({
    title,
    body: data.text,
    silent: false,
  })
  notification.show()

  // 5    
  setTimeout(() => {
    notification.close()
  }, 5000)
})

ipcMain.handle('get-username', () => {
  return os.userInfo().username || ''
})

ipcMain.on('hide-sender', () => {
  if (mainWindow) mainWindow.hide()
})

app.on('window-all-closed', () => {
  app.quit()
})

ipcMain.on('message', async (event, arg) => {
  event.reply('message', `${arg} World!`)
})
